.banner {
	color: white;
	object-fit: contain;
	height: 448px;
	padding-top: 150px;

}
.banner__contents {
	margin-left: 30px;
	padding-top: 140px;
	height: 190px;
}
.banner__title {
	font-size: 3 rem;
	font-weight: 800;
	padding-bottom: 1rem;
}
.banner__button {
	cursor: pointer;
	color: #fff;
	outline: none;
	border: none;
	font-weight: 700;
	border-radius: 0.2vw;
	padding: 0.5rem 2rem;
	margin-right: 1rem;
	background-color: rgba(51, 51, 51, 0.5);
	font-size: 1rem;
    transition: background 0.3s ease;
}

.banner__button:hover {
	color: #000;
	background-color: #e70f0f;
}

.banner__description {
	width: 45rem;
	line-height: 1.3;
	padding-top: 1rem;
	font-size: 0.8rem;
	max-width: 360px;
	height: 80px;
}

.banner__fadeBottom {
	height: 7.4rem;
	background-image: linear-gradient(
		180deg,
		transparent,
		rgb(37, 37, 37, 0.61),
		#111
	);
}